import React from 'react';
import FlowComponent from '../../common/component/core/FlowComponent';

import {Form, Col , Button } from 'react-bootstrap';
import DatePicker from 'react-datetime';



class SearchFieldVisit extends FlowComponent{

	

componentWillMount(){

	
	this.load_flow_rel_mgr();
}






render(){
	return(


		<Form >
         
	        <Form.Row>

	            <Form.Group as={Col} className="col-3">
	             	<Form.Label>Flow RM</Form.Label><font className="text-danger">   *  </font>
			      	<Form.Control as="select" id="flow_rel_mgr_id" onChange={(e) =>{this.props.onComponentElementChange(e)}}>
			      	{this.state.flow_rel_mgr_dd}
	                </Form.Control>
	                  
	                
		       	</Form.Group>

		       	
			     <Form.Group as={Col} className="col-2">
			    	<Form.Label>Date</Form.Label><font className="text-danger">   *  </font>

			    	<DatePicker closeOnSelect={true}  id="date" dateFormat="DD MMM YYYY" timeFormat={false} required="required" onChange={(value) => {this.handleDateChange("visit_start_time", value)}} />
			    </Form.Group>
			    <Form.Group as={Col}>
		       	</Form.Group>
	        </Form.Row>
	        <Form.Row className="justify-content-md-center">
	        	<button type="button" className="btn btn-primary " id="search" onClick={this.props.onSearch} >Search</button>
	        </Form.Row>
	    </Form >)
}

}


export default SearchFieldVisit;